import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {HttpClientModule} from "@angular/common/http";

import { AppComponent } from './app.component';
import { LoginComponent } from './auth/login/login.component';

import {FormsModule , ReactiveFormsModule} from '@angular/forms';
import {AppRoutesModule} from './app-routes/app-routes.module';
import { HomeModule } from './home/home.module';
import {ReleasemanagementModule} from './release-management/release-management.module';
import {JgitService} from './shared/service/jgit.service';
import {ReleaseService} from './shared/service/release.service';


import {
  FooterComponent,
  HeaderComponent,
  SharedModule
} from './shared';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule , FormsModule , ReactiveFormsModule, NgbModule.forRoot(),AppRoutesModule,
    HomeModule,ReleasemanagementModule,SharedModule,HttpClientModule
  ],
  providers: [JgitService,ReleaseService],
  bootstrap: [AppComponent]
})
export class AppModule { }



