import { Component, OnInit } from '@angular/core';
import {JgitService} from '../../shared/service/jgit.service';
import {ReleaseService} from '../../shared/service/release.service';
import {releaseModel} from '../../shared/model/release.model';
import { ReleasemanagementModule } from '../release-management.module';
import * as $ from 'jquery';


@Component({
  selector: 'app-add-release',
  templateUrl: './add-release.component.html',
  styleUrls: ['./add-release.component.css']
  
})
export class AddReleaseComponent implements OnInit {

  selectedFiles:any[];
  currentDate=new Date();
  res:any;
  releaseObj:releaseModel;
  releaseName:string;
  comment:string;

  constructor(private jgitService:JgitService,private releaseSer:ReleaseService) { }

  ngOnInit() {
    this.selectedFiles=this.jgitService.fetchSelectedFiles();
    console.log("INIT METHOD OF AddReleaseComponent::::"+this.selectedFiles.length);
  }

  createRelease(){
    console.log("createRelease METHOD OF AddReleaseComponent::::");
    this.releaseObj={
      releasename:this.releaseName,
      comment:this.comment,
      createdBy:"test",
      createdDate:new Date(),
      selectedFilesList:this.selectedFiles
    }
    
    this.releaseSer.createRelease(this.releaseObj).subscribe(b => this.res = b);;

  }

  toggleAccordian = function(targetId){
    
    $("#tabsJustifiedContent>div.active").removeClass("active");
    $("#tabsJustifiedContent>div.show").removeClass("show");
    $("#"+targetId).addClass("active");
    $("#"+targetId).addClass("show");
  }

  addActiveClas = function(targetId){
    $("#tabsJustified>li>a.active").removeClass("active");
    $("#"+targetId).addClass("active");
  }

}



