import { Injectable } from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {environment} from '../../../environments/environment';


@Injectable()
export class JgitService{

    public static API_URL:string= environment.API_URL;//"http://localhost:9090/";
    public selectedFilesList:any=[];

    constructor(private _http:HttpClient) { }


    fetchFilesAndFolders():Observable<any[]>{
        console.log("fetchFilesAndFolders method of JgitService"+JgitService.API_URL);
        return this._http.get<any[]>(JgitService.API_URL+"ReadFiles/listFiles?path=");
    }

    fetchFilesAndFoldersInSubDirectory(path):Observable<any[]>{
        console.log("fetchFilesAndFolders method of JgitService"+JgitService.API_URL);
        return this._http.get<any[]>(JgitService.API_URL+"ReadFiles/listFiles?path="+path);
    }

    addSelectedFilesToList(fileObj:any){
        console.log("addSelectedFilesToList of jgit service called:::::");
        this.selectedFilesList.push(fileObj);
        console.log("addSelectedFilesToList list:::::"+this.selectedFilesList.length);
    }

    fetchSelectedFiles(){
        console.log("fetchSelectedFiles of jgit service called:::::"+this.selectedFilesList.length);
        return this.selectedFilesList;
    }

    removeItemFromList(index:any){
        this.selectedFilesList.splice(index, 1); 
    }

    fetchFilesByCommit(fromDate:string,toDate:string):Observable<any[]>{
        console.log("fetchFilesByCommit method of JgitService"+JgitService.API_URL);
        return this._http.get<any[]>(JgitService.API_URL+"ReadFiles/listCommits?fromDate="+fromDate+"&toDate="+toDate);
    }

    fetchFilesInACommit(name:string):Observable<any[]>{
        console.log("fetchFilesInACommit method of JgitService"+JgitService.API_URL);
        return this._http.get<any[]>(JgitService.API_URL+"ReadFiles/listFilesInCommit?name="+name);
    }


}


