import { Injectable } from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {environment} from '../../../environments/environment';

@Injectable()
export class ReleaseService{

    public static API_URL:string= environment.API_URL;

    constructor(private _http:HttpClient) { }

    createRelease(release:any):Observable<any[]>{
        console.log("inside createRelease:::::"+release);
        var url = ReleaseService.API_URL+"Release/createRelease"
        return this._http.post<any>(url, release);
    }

}


