import {FilesByCommit} from './add-release/browse-files/files-by-commit.component';

@NgModule({
  imports: [
    CommonModule,FormsModule,AppRoutesModule
  ],
  providers: [],
  declarations: [ AddReleaseComponent, AddFilesComponent, BrowseFilesComponent, ExistingReleaseComponent,FilesByCommit]
})
export class ReleasemanagementModule { }



