import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-existing-release',
  templateUrl: './existing-release.component.html',
  styleUrls: ['./existing-release.component.css']
})
export class ExistingReleaseComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  toggleAccordian = function(targetId){
    
    $("#tabsJustifiedContent>div.active").removeClass("active");
    $("#tabsJustifiedContent>div.show").removeClass("show");
    $("#"+targetId).addClass("active");
    $("#"+targetId).addClass("show");
  }

  addActiveClas = function(targetId){
    $("#tabsJustified>li>a.active").removeClass("active");
    $("#"+targetId).addClass("active");
  }

}



