import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {RouterModule , Routes} from '@angular/router';
import { LoginComponent } from '../auth/login/login.component';
import { HomeComponent } from '../home/home.component';
import {AddReleaseComponent} from '../release-management/add-release/add-release.component';
import {AddFilesComponent} from '../release-management/add-release/add-files/add-files.component';
import {BrowseFilesComponent} from '../release-management/add-release/browse-files/browse-files.component';
import { ExistingReleaseComponent } from '../release-management/existing-release/existing-release.component';
import {FilesByCommit} from '../release-management/add-release/browse-files/files-by-commit.component';



const routes : Routes = [
  {path:'' , component :  LoginComponent },
  {path : 'home' , component : HomeComponent},
  {path : 'addRelease' , component : AddReleaseComponent},
  {path : 'addFiles',component:AddFilesComponent,pathMatch:'prefix',
    children: [
      {path: '', redirectTo:'browseFiles',pathMatch: 'full'},
      {path: 'browseFiles', component: BrowseFilesComponent,pathMatch: 'full'},
      {path: 'filesByCommit', component: FilesByCommit,pathMatch: 'full'},
      {path: 'existingRelease', component: ExistingReleaseComponent,pathMatch: 'full'}
    //{path: '', redirectTo: 'browseFiles'}, 
    
  ]
  },
  //{path : 'browseFiles',component:BrowseFilesComponent},
  {path : 'existingRelease',component:ExistingReleaseComponent}
];


@NgModule({
  imports: [
    RouterModule.forRoot(routes,{useHash: true})
  ],
  exports : [RouterModule],
  declarations: []
})
export class AppRoutesModule { 




}



